body{
	color: #fff;
	font-family: "Open Sans", Helvetica, sans-serif;
	font-size: 12;
	font-weight: 400;
	letter-spacing: .075em;
	line-height: 1.65em;
	background-color: #2e3842;
	margin:0;
	padding:0;
	border: 0;
	vertical-align: baseline;
	display: block;
	background-image:url('./assets/images/GardenOfThings.png');
	background-repeat: no-repeat;
  background-attachment: fixed;
	background-size:cover;
}

h1{
	left: 1.25em;
	position: absolute;
	top:0;
	font-weight: 800;
	letter-spacing: .225em;
	margin: 0 0 1em 0;
	text-transform: uppercase;
}

h2{
	font-size: 1.35em;
	line-height: 1.75em;
	font-weight: 800;
	letter-spacing: .225em;
	margin: 0 0 1em 0;
	text-transform: uppercase;
	border-bottom: solid 2px #fff;
	display: inline-block;
}
h2:after{
	content: '';
	display:block;
	height: 1px;
}

h4{
	font-size: 1.35em;
	line-height:1.75em;
	font-weight: 800;
	color: #fff;
	letter-spacing: 0.225em;
	margin:0 0 1em 0;
	text-transform: uppercase;
}

p{
	margin: 0 0 2em 0;
}

a{
	color: inherit;
	text-decoration: none;
	width: inherit;
	height: inherit;

}

img{
	display:block;
	width: 100%;
	height: 100%;
}

nav{
	height: inherit;
	right: 1.25em;
	line-height: inherit;
	position: absolute;
	top:0;

	font-weight: 100;
	letter-spacing: .225em;
	margin: 0 0 1em 0;
	text-transform: uppercase;
	font-size: 0.8em;
}


.inner{
	margin: 0 auto;
	height: 200px;
	position: relative;
	top: 40%;
}
