.jumbotron{
  background-color: #21b2a6;
  color: #c8ece9;
  /* text-align:center; */
  padding: 6em 0 4em 0;
  margin: 0 0 7em 0;
  border: 1px solid #21b2a6;

}

.jumbotronContent{
  width: 90%;
  margin: 0 auto;
}

.graphContainer {
    border: 4px solid #2e3842;
    margin: 0 auto 4em auto;
    width: 50vw;
    height: 50vh;
    background: white;
    padding: 0 20px 10px 20px;
    border-radius: 10px;
    -webkit-box-shadow: 0px 10px 0px -5px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 10px 0px -5px rgba(0,0,0,0.3);
    box-shadow: 0px 10px 0px -5px rgba(0,0,0,0.3);
}

@media only screen and (max-width: 600px) {
    .graphContainer {
        width: 80vw;
    }
}
